import { GlobalParameter } from '../models/dtos/ger/globalParameter';

export function hasGrupoPlanoContas(globalParameter: GlobalParameter): string | undefined {
  let result = null;

  const msg: string =
    ' O Grupo de Plano de Contas selecionado não está configurado. Para configuar acesse:' +
    'Módulo Contábil > Manutenção > Grupos > Plano de Contas - \n' +
    'E configure os seguintes campos: Máscara, Dígito: Ativo, Passivo, Receita e Despesa.';

  if (
    !globalParameter.grupoEmpresaConfig &&
    !globalParameter.grupoPlanoConta &&
    !globalParameter.grupoPlanoConta.mascara &&
    !globalParameter.grupoPlanoConta.digitoAtivo &&
    !globalParameter.grupoPlanoConta.digitoPassivo &&
    !globalParameter.grupoPlanoConta.digitoReceita &&
    !globalParameter.grupoPlanoConta.digitoDespesa
  ) {
    result = msg;
  }

  return result;
}
