import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import moment from 'moment';
import React, { FC, useEffect, useState } from 'react';
import BootstrapTable from 'react-bootstrap-table-next';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Button, Col, Form, Modal, ModalBody, ModalFooter, ModalHeader, Row, UncontrolledTooltip } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  Box,
  ColumnsProps,
  decimalFormatter,
  ErroList,
  ERROR,
  formatRef,
  formatSaldo,
  InputGroupInline,
  InputType,
  isEmpty,
  progressActions,
  SelectModeEnum,
  showToast,
  Tab,
  TabGroup,
  toDate,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { hasGrupoPlanoContas } from '../../../../dependencies/grupoPlanoContasDependency';
import { PlanoConta } from '../../../../models/dtos/ger/planoConta';
import { ConciliacaoConta } from '../../../../models/dtos/mcg/conciliacaoConta';
import { LancamentoConciliacao } from '../../../../models/dtos/mcg/lancamentoConciliacao';
import { CODIGO_REDUZIDO as CODIGO_REDUZIDO_PLANO } from '../../../../models/enumerated/ger/planoContaGERTipoLancamentoContaEnum';
import {
  conciliarLancamentosConta,
  obterLancamentosSaldoPeriodo,
  removerConciliarLancamentosConta,
  removerLancamentoLote,
} from '../../../../services/mcg.service';
import { multiPageActions, Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import { OPERACOES_CONSULTA_VALORES } from '../../../../utilities/constantes';
import { alertInformation, promisedAlertWithCatch } from '../../../../utilities/mcg.util';
import PlanoContaIS from '../../../components/mcg/planoContaIS/PlanoContaIS';
import LancamentoSaldoEditView from '../lancamentoSaldo/LancamentoSaldoEditView';

const ConciliacaoContaView: FC<RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm({ mode: 'onBlur' });
  const dispatch = useDispatch();
  const [lancamentosNConciliadoList, setLancamentosNConciliadoList] = useState([]);
  const [lancamentosConciliadosList, setLancamentosConciliadosList] = useState([]);
  const [lancamentoNConciliadoFilter, setLancamentoNConciliadoFilter] = useState([]);
  const [showModalViewConciliar, setShowModalViewConciliar] = useState(false);
  const [showModalRemoverConciliacao, setShowModalRemoverConciliacao] = useState(false);
  const [selectedItemsLancParaConciliar, setSelectedItemsLancParaConciliar] = useState([]);
  const [informacaoConciliacao, setInformacaoConciliacao] = useState('');
  const [lancSelecionadoNConciliado, setLancSelecionadoNConciliado] = useState<LancamentoConciliacao>(null);
  const [lancSelecionadoConciliado, setLancSelecionadoConciliado] = useState<LancamentoConciliacao>(null);
  const [lancamentoConciliadoCredito, setLancamentoConciliadoCredito] = useState([]);
  const [lancamentoConciliadoDebito, setLancamentoConciliadoDebito] = useState([]);
  const [saldoInicial, setSaldoInicial] = useState(0);
  const [saldoAConciliar, setSaldoAConciliar] = useState(0);
  const [saldoConciliado, setSaldoConciliado] = useState(0);
  const [saldoAtual, setSaldoAtual] = useState(0);
  const [somarCredito, setSomarCredito] = useState(false);
  const [somarDebito, setSomarDebito] = useState(false);
  const [valorLancParaConciliar, setValorLancParaConciliar] = useState(0);
  const [removerLancamento, setRemoverLancamento] = useState(false);
  const [editLacamento, setEditLacamento] = useState(false);

  const contaDebitoOuCRFormatter = (cell, row) => {
    let result = '';
    if (globalReducer.globalParameter.empresaMCG.tipoLancamentoConta === CODIGO_REDUZIDO_PLANO.value) {
      if (row.crDebito) {
        result = row.crDebito;
      }
    } else {
      if (row.contaDebito) {
        result = row.contaDebito;
      }
    }

    return result;
  };

  const contaCreditoOuCRFormatter = (cell, row) => {
    let result = '';

    if (globalReducer.globalParameter.empresaMCG.tipoLancamentoConta === CODIGO_REDUZIDO_PLANO.value) {
      if (row.crCredito) {
        result = row.crCredito;
      }
    } else {
      if (row.contaCredito) {
        result = row.contaCredito;
      }
    }

    return result;
  };

  const showLoading = () =>
    progressActions.showProgress({
      animated: true,
      className: 'progress-bar-rounded progress-animated-alt',
      color: 'first',
      label: 'Aguarde processando...',
      striped: true,
      value: 100,
    });

  const columns: ColumnsProps[] = [
    {
      dataField: 'nro',
      sort: true,
      text: 'Nº Lançamento',
    },
    {
      dataField: 'dt',
      sort: true,
      text: 'Data',
    },
    {
      dataField: 'crDebito',
      sort: true,
      text: 'CR Débito',
    },
    {
      dataField: 'contaDebito',
      sort: true,
      text: 'Débito',
    },
    {
      dataField: 'crCredito',
      text: 'CR Crédito',
    },
    {
      dataField: 'contaCredito',
      text: 'Crédito',
    },
    {
      dataField: 'historico',
      sort: true,
      text: 'Histórico',
    },
    {
      dataField: 'valor',
      formatter: decimalFormatter,
      sort: true,
      text: 'Valor do Lançamento',
    },
    {
      dataField: 'saldo',
      formatter: formatSaldo,
      sort: true,
      text: 'Saldo da Conta',
    },
  ];

  const columnsNConciliados: ColumnsProps[] = [
    {
      dataField: 'nro',
      sort: true,
      text: 'Nº Lançamento',
    },
    {
      dataField: 'dt',
      sort: true,
      text: 'Data',
    },
    {
      dataField: 'codigoReduzidoDebito',
      formatter: contaDebitoOuCRFormatter,
      sort: true,
      text: 'Débito',
    },
    {
      dataField: 'codigoReduzidoCredito',
      formatter: contaCreditoOuCRFormatter,
      text: 'Crédito',
    },
    {
      dataField: 'historico',
      sort: true,
      text: 'Histórico',
    },
    {
      dataField: 'valor',
      formatter: decimalFormatter,
      sort: true,
      text: 'Valor do Lançamento',
    },
    {
      dataField: 'saldo',
      formatter: formatSaldo,
      sort: true,
      text: 'Saldo da Conta',
    },
    {
      dataField: 'conciliar',
      formatter: (cell, row) => {
        return (
          <Button id={'buttonConciliar' + row.id} onClick={openModalViewConciliar} className="btn btn-outline-primary btn-sm">
            <span className="btn-wrapper--icon">
              <FontAwesomeIcon color={'primary'} icon={'cog'} />
            </span>
            <UncontrolledTooltip target={'buttonConciliar' + row.id} placement="top">
              Conciliar Lançamento
            </UncontrolledTooltip>
          </Button>
        );
      },
      headerAlign: () => {
        return 'center';
      },
      style: (cell, row) => {
        return { backgroundColor: row.color, color: 'white', textAlign: 'center' };
      },
      text: 'Conciliar Lançamentos',
    },
    {
      dataField: 'actions',
      formatter: (cell, row) => {
        return (
          <>
            <Button
              id={'buttonEditNConciliados' + row.id}
              color="neutral-secondary"
              className="btn-pill d-30 p-0 align-top btn-animated-icon btn-animated-icon--secondary"
              onClick={openEditLancamento}
            >
              <span className="btn-wrapper--icon">
                <FontAwesomeIcon color={'black'} icon={'pen'} />
              </span>
              <UncontrolledTooltip target={'buttonEditNConciliados' + row.id} placement="top">
                Alterar Lançamento
              </UncontrolledTooltip>
            </Button>
            <Button
              id={'buttonRemoveNConciliados' + row.id}
              color="neutral-secondary"
              onClick={removerLancamentoNConciliado}
              className="btn-pill d-30 p-0 align-top btn-animated-icon btn-animated-icon--secondary"
            >
              <span className="btn-wrapper--icon">
                <FontAwesomeIcon color={'black'} icon={'times'} />
              </span>
              <UncontrolledTooltip target={'buttonRemoveNConciliados' + row.id} placement="top">
                Remover Lançamento
              </UncontrolledTooltip>
            </Button>
          </>
        );
      },
      headerAlign: () => {
        return 'center';
      },
      style: (cell, row) => {
        return { backgroundColor: row.color, color: 'white', textAlign: 'center' };
      },
      text: 'Ações',
    },
  ];

  const columnsConciliados: ColumnsProps[] = [
    {
      dataField: 'nro',
      sort: true,
      text: 'Nº Lançamento',
    },
    {
      dataField: 'dt',
      sort: true,
      text: 'Data',
    },
    {
      dataField: 'codigoReduzidoDebito',
      formatter: contaDebitoOuCRFormatter,
      sort: true,
      text: 'Débito',
    },
    {
      dataField: 'codigoReduzidoCredito',
      formatter: contaCreditoOuCRFormatter,
      text: 'Crédito',
    },
    {
      dataField: 'historico',
      sort: true,
      text: 'Histórico',
    },
    {
      dataField: 'valor',
      formatter: decimalFormatter,
      sort: true,
      text: 'Valor do Lançamento',
    },
    {
      dataField: 'saldo',
      formatter: formatSaldo,
      sort: true,
      text: 'Saldo da Conta',
    },
    {
      dataField: 'removerConciliacao',
      formatter: (cell, row) => {
        return (
          <Button id={'buttonRemoveConciliacao' + row.id} onClick={openModalViewRemoverConciliacao} className="btn btn-outline-primary btn-sm">
            <span className="btn-wrapper--icon">
              <FontAwesomeIcon color={'primary'} icon={'trash'} />
            </span>
            <UncontrolledTooltip target={'buttonRemoveConciliacao' + row.id} placement="top">
              Remover Conciliação
            </UncontrolledTooltip>
          </Button>
        );
      },
      headerAlign: () => {
        return 'center';
      },
      style: (cell, row) => {
        return { backgroundColor: row.color, color: 'white', textAlign: 'center' };
      },
      text: 'Remover Conciliação',
    },
    {
      dataField: 'actions',
      formatter: (cell, row) => {
        return (
          <>
            <Button
              id={'buttonEditConciliado' + row.id}
              color="neutral-secondary"
              className="btn-pill d-30 p-0 align-top btn-animated-icon btn-animated-icon--secondary"
              onClick={openEditLancamento}
            >
              <span className="btn-wrapper--icon">
                <FontAwesomeIcon color={'black'} icon={'pen'} />
              </span>
              <UncontrolledTooltip target={'buttonEditConciliado' + row.id} placement="top">
                Alterar Lançamento
              </UncontrolledTooltip>
            </Button>
            <Button
              id={'buttonRemoveConciliado' + row.id}
              color="neutral-secondary"
              onClick={removerLancamentoConciliado}
              className="btn-pill d-30 p-0 align-top btn-animated-icon btn-animated-icon--secondary"
            >
              <span className="btn-wrapper--icon">
                <FontAwesomeIcon color={'black'} icon={'times'} />
              </span>
              <UncontrolledTooltip target={'buttonRemoveConciliado' + row.id} placement="top">
                Remover Lançamento
              </UncontrolledTooltip>
            </Button>
          </>
        );
      },
      headerAlign: () => {
        return 'center';
      },
      style: (cell, row) => {
        return { backgroundColor: row.color, color: 'white', textAlign: 'center' };
      },
      text: 'Ações',
    },
  ];

  const handleSelectLancNConciliado = (rowLancamentoConciliacao: LancamentoConciliacao) => {
    setLancSelecionadoNConciliado(rowLancamentoConciliacao);
  };

  const handleSelectLancParaConciliar = (row, isSelect) => {
    const resultadoSelectedLancamentos = isSelect
      ? [...selectedItemsLancParaConciliar, row]
      : selectedItemsLancParaConciliar.filter(it => it.id !== row.id);

    setValorLancParaConciliar(isSelect ? valorLancParaConciliar + parseFloat(row.valor) : valorLancParaConciliar - parseFloat(row.valor));
    setSelectedItemsLancParaConciliar(resultadoSelectedLancamentos);
  };

  const handleSelectLancConciliados = (rowLancamentoConciliacao: LancamentoConciliacao) => {
    setLancSelecionadoConciliado(rowLancamentoConciliacao);
  };

  function handleCloseModal(): void {
    setShowModalViewConciliar(false);
    setLancamentoNConciliadoFilter([]);
  }

  function handleCloseModalRemoverConciliados() {
    setShowModalRemoverConciliacao(false);
  }

  const handleBlurPeriodoDe = () => {
    const periodoAte = moment(toDate(formProps.getValues().periodoDe)).endOf('month');
    formProps.setValue('periodoAte', periodoAte, true);
  };

  const selectRowLancamentoNConciliado = {
    clickToSelect: true,
    mode: SelectModeEnum.RADIO,
    onSelect: handleSelectLancNConciliado,
  };

  const selectRowLancamentoParaConciliar = {
    clickToSelect: true,
    mode: SelectModeEnum.CHECKBOX,
    onSelect: handleSelectLancParaConciliar,
  };

  const selectRowLancamentosConciliados = {
    clickToSelect: true,
    mode: SelectModeEnum.RADIO,
    onSelect: handleSelectLancConciliados,
  };

  const validaBefore = () => {
    const erros = validateExtras();
    if (erros.length > 0) {
      const params: AlertProps = {
        message: <ErroList erros={erros} />,
        type: AlertType.WARNING,
      };
      alertManager.emit(ACTION_EVENTS.SHOW, params);
    } else {
      dispatch(showLoading());
      obterLancSaldoPeriodo();
    }
  };

  useEffect(() => {
    if (!isEmpty(lancSelecionadoConciliado) && editLacamento) {
      openMultiPage(LancamentoSaldoEditView, lancSelecionadoConciliado);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [editLacamento, lancSelecionadoConciliado]);

  useEffect(() => {
    if (!isEmpty(lancSelecionadoNConciliado) && editLacamento) {
      openMultiPage(LancamentoSaldoEditView, lancSelecionadoNConciliado);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [editLacamento, lancSelecionadoNConciliado]);

  const openEditLancamento = () => {
    setEditLacamento(true);
  };

  const openMultiPage = (componente: any, itemSelect: any) => {
    const Page = componente as FC<any>;
    const pageProps = {
      history: props.history,
      location: props.location,
      match: props.match,
      selectedItem: {
        idLote: Number(itemSelect.idLote),
      },
    };
    dispatch(multiPageActions.showPage(<Page {...pageProps} />));
  };

  const getSaldoInicialFmt = () => {
    let valorInicial = 0;
    if (lancamentosNConciliadoList.length > 0) {
      valorInicial = lancamentosNConciliadoList[0].saldo + lancamentosNConciliadoList[0].valor;
    } else {
      valorInicial = 0;
    }
    setSaldoInicial(valorInicial);
  };

  const calcularSaldoAConciliar = () => {
    let saldoAConciliarCalculado: number = 0;
    if (lancamentosNConciliadoList.length > 0) {
      lancamentosNConciliadoList.forEach((lancamentoConciliacao: LancamentoConciliacao) => {
        saldoAConciliarCalculado += lancamentoConciliacao.valor;
      });
    } else {
      saldoAConciliarCalculado = 0;
    }
    setSaldoAConciliar(saldoAConciliarCalculado);
  };

  const calcularSaldoConciliado = () => {
    let saldoConciliadoCalculado: number = 0;
    if (lancamentosConciliadosList.length > 0) {
      lancamentosConciliadosList.forEach((lancamentoConciliacao: LancamentoConciliacao) => {
        saldoConciliadoCalculado += lancamentoConciliacao.valor;
      });
    } else {
      saldoConciliadoCalculado = 0;
    }
    setSaldoConciliado(saldoConciliadoCalculado);
  };

  const getSaldoAtual = () => {
    let saldoAtualResult: number = 0;
    if (lancamentosNConciliadoList.length > 0) {
      saldoAtualResult = lancamentosNConciliadoList[lancamentosNConciliadoList.length - 1].saldo;
    } else {
      saldoAtualResult = 0;
    }
    setSaldoAtual(saldoAtualResult);
  };

  const obterLancSaldoPeriodo = () => {
    const idGrupoPlanoConta = formProps.getValues().planoConta.id;
    const idEmpresa = globalReducer.globalParameter.empresa.id;
    const dtInicial = formProps.getValues().periodoDe;
    const dtFinal = formProps.getValues().periodoAte;
    const historico = formProps.getValues().historico;
    const valor = formProps.getValues().valor;
    const operacaoConsultaValor = formProps.getValues().operacaoConsultaValor;
    const lancamentosNConciliados = [];
    const lancamentosConciliados = [];

    obterLancamentosSaldoPeriodo(idGrupoPlanoConta, idEmpresa, dtInicial, dtFinal, historico, valor, operacaoConsultaValor, {
      errorFunction: (mensagem: string): void => {
        showToast(mensagem, ERROR);
      },
      thenFunction: (lancamentosConciliacao: LancamentoConciliacao[]): void => {
        dispatch(progressActions.hideProgress());

        lancamentosConciliacao.forEach((lancamentoConciliacao: LancamentoConciliacao) => {
          if (lancamentoConciliacao.conciliado) {
            lancamentosConciliados.push(lancamentoConciliacao);
          } else {
            lancamentosNConciliados.push(lancamentoConciliacao);
          }
        });

        if (lancamentosConciliados.length > 0) {
          setLancamentosConciliadosList(lancamentosConciliados);
        } else {
          setLancamentosConciliadosList([]);
        }

        if (lancamentosNConciliados.length > 0) {
          setLancamentosNConciliadoList(lancamentosNConciliados);
        } else {
          setLancamentosNConciliadoList([]);
        }
      },
    });
  };

  const mostrarCodigoReduzido = (): boolean => {
    return globalReducer.globalParameter.empresaMCG.tipoLancamentoConta === CODIGO_REDUZIDO_PLANO.value;
  };

  const preModalConciliar = () => {
    setEditLacamento(false);
    if (!isEmpty(formProps.getValues().planoConta)) {
      const contaSelecionada: PlanoConta = formProps.getValues().planoConta;
      const conta = mostrarCodigoReduzido() ? contaSelecionada.codigoReduzido : contaSelecionada.conta;

      if (lancSelecionadoNConciliado.contaCredito === contaSelecionada.conta) {
        const lancamentosNConciliadoFilter = lancamentosNConciliadoList.filter((it: LancamentoConciliacao) => {
          return it.contaCredito !== contaSelecionada.conta;
        });
        setSomarCredito(true);
        setSomarDebito(false);
        setLancamentoNConciliadoFilter(lancamentosNConciliadoFilter);
        setInformacaoConciliacao(`Lançamentos de contra partida de crédito da conta de débito ${conta} selecionada.`);
      }

      if (lancSelecionadoNConciliado.contaDebito === contaSelecionada.conta) {
        const lancamentosNConciliadoFilter = lancamentosNConciliadoList.filter((it: LancamentoConciliacao) => {
          return it.contaDebito !== contaSelecionada.conta;
        });
        setSomarDebito(true);
        setSomarCredito(false);
        setLancamentoNConciliadoFilter(lancamentosNConciliadoFilter);
        setInformacaoConciliacao(`Lançamentos de contra partida de débito da conta de crédito ${conta} selecionada.`);
      }
    }
  };

  const conciliar = () => {
    let debitoCreditoMarcado: boolean;
    const listaConciliacao: ConciliacaoConta[] = [];

    if (!isEmpty(formProps.getValues().planoConta)) {
      const contaSelecionada: PlanoConta = formProps.getValues().planoConta;

      if (lancSelecionadoNConciliado.contaCredito === contaSelecionada.conta) {
        debitoCreditoMarcado = true;
      } else if (lancSelecionadoNConciliado.contaDebito === contaSelecionada.conta) {
        debitoCreditoMarcado = false;
      }

      if (selectedItemsLancParaConciliar.length > 0) {
        selectedItemsLancParaConciliar.forEach((lancamentoConciliacao: LancamentoConciliacao) => {
          const conciliacaoConta: ConciliacaoConta = {};
          conciliacaoConta.planoConta = contaSelecionada;
          if (debitoCreditoMarcado) {
            conciliacaoConta.lancamentoCredito = {
              id: lancSelecionadoNConciliado.id,
            };
            conciliacaoConta.lancamentoDebito = {
              id: lancamentoConciliacao.id,
            };
          } else {
            conciliacaoConta.lancamentoDebito = {
              id: lancSelecionadoNConciliado.id,
            };
            conciliacaoConta.lancamentoCredito = {
              id: lancamentoConciliacao.id,
            };
          }
          listaConciliacao.push(conciliacaoConta);
        });
        conciliarLancamentosConta(listaConciliacao, {
          errorFunction: (mensagem: string): void => {
            showToast(mensagem, ERROR);
          },
          thenFunction: (): void => {
            dispatch(progressActions.hideProgress());
            alertInformation('Os Lançamentos Selecionados Foram Conciliados.');
            obterLancSaldoPeriodo();
            setShowModalViewConciliar(false);
            setSelectedItemsLancParaConciliar([]);
            setLancSelecionadoNConciliado(null);
            setValorLancParaConciliar(0);
          },
        });
      } else {
        alertInformation('É necessário que pelo menos um lançamento esteja selecionado para conciliar.');
      }
    }
  };

  useEffect(() => {
    if (showModalViewConciliar && !isEmpty(lancSelecionadoNConciliado)) {
      preModalConciliar();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [lancSelecionadoNConciliado, showModalViewConciliar]);

  const openModalViewConciliar = () => {
    setShowModalViewConciliar(true);
  };

  const preModalRemoverConciliacao = () => {
    setEditLacamento(false);
    const listaLancamentosConciliados: ConciliacaoConta[] = lancSelecionadoConciliado.listaLancamentosConciliados;
    const contaSelecionada: PlanoConta = formProps.getValues().planoConta;
    const listLancConciliadoCredito = [];
    const listLancConciliadoDebito = [];

    if (lancSelecionadoConciliado.contaCredito === contaSelecionada.conta) {
      listLancConciliadoCredito.push(lancSelecionadoConciliado);
      listaLancamentosConciliados.forEach((conciliacaoConta: ConciliacaoConta) => {
        lancamentosConciliadosList.forEach((lancamentoConciliacao: LancamentoConciliacao) => {
          if (conciliacaoConta.lancamentoDebito.id === lancamentoConciliacao.id) {
            listLancConciliadoDebito.push(lancamentoConciliacao);
          }
        });
      });
    }
    if (lancSelecionadoConciliado.contaDebito === contaSelecionada.conta) {
      listLancConciliadoDebito.push(lancSelecionadoConciliado);
      listaLancamentosConciliados.forEach((conciliacaoConta: ConciliacaoConta) => {
        lancamentosConciliadosList.forEach((lancamentoConciliacao: LancamentoConciliacao) => {
          if (conciliacaoConta.lancamentoCredito.id === lancamentoConciliacao.id) {
            listLancConciliadoCredito.push(lancamentoConciliacao);
          }
        });
      });
    }

    setLancamentoConciliadoDebito(listLancConciliadoDebito);
    setLancamentoConciliadoCredito(listLancConciliadoCredito);
  };

  const removerConciliacao = () => {
    const listaConciliarRemover: ConciliacaoConta[] = lancSelecionadoConciliado.listaLancamentosConciliados;

    removerConciliarLancamentosConta(listaConciliarRemover, {
      errorFunction: (mensagem: string): void => {
        showToast(mensagem, ERROR);
      },
      thenFunction: (): void => {
        dispatch(progressActions.hideProgress());
        alertInformation('A Conciliação Selecionada Foi Removida.');
        obterLancSaldoPeriodo();
        setShowModalRemoverConciliacao(false);
        setLancSelecionadoNConciliado(null);
        setValorLancParaConciliar(0);
      },
    });
  };

  useEffect(() => {
    if (showModalRemoverConciliacao && !isEmpty(lancSelecionadoConciliado)) {
      preModalRemoverConciliacao();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [lancSelecionadoConciliado, showModalRemoverConciliacao]);

  const openModalViewRemoverConciliacao = () => {
    setShowModalRemoverConciliacao(true);
  };

  useEffect(() => {
    getSaldoInicialFmt();
    calcularSaldoAConciliar();
    calcularSaldoConciliado();
    getSaldoAtual();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [lancamentosNConciliadoList, lancamentosConciliadosList]);

  const validateExtras = (): string[] => {
    const erros = [];

    if (isEmpty(formProps.getValues().periodoDe)) {
      erros.push('O filtro de data inicial é de preenchimento obrigatório.');
    }

    if (isEmpty(formProps.getValues().periodoAte)) {
      erros.push('O filtro de data final é de preenchimento obrigatório.');
    }

    if (isEmpty(formProps.getValues().planoConta)) {
      erros.push('Selecione uma Conta Contábil para consultar.');
    }

    if (toDate(formProps.getValues().periodoDe) > toDate(formProps.getValues().periodoAte)) {
      erros.push('A Data Inicial deve ser menor que a Data Final.');
    }

    if (!isEmpty(formProps.getValues().planoConta)) {
      if (!formProps.getValues().planoConta.analitica) {
        erros.push('A conta selecionada deve ser analítica.');
      }
    }

    return erros;
  };

  const removerLancamentos = selectedItem => {
    const refExclusao = formatRef(toDate(selectedItem.dt));
    removerLancamentoLote(
      {
        empresaMCG: globalReducer.globalParameter.empresaMCG,
        listItem: [selectedItem],
        referencia: refExclusao,
        removerLote: false,
      },
      {
        thenFunction: res => {
          alertInformation('Lançamento excluido com sucesso.');
        },
      }
    );
  };

  useEffect(() => {
    if (removerLancamento && !isEmpty(lancSelecionadoConciliado)) {
      removerLancamentos(lancSelecionadoConciliado);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [lancSelecionadoConciliado, removerLancamento]);

  const removerLancamentoConciliado = () => {
    promisedAlertWithCatch('Tem certeza que deseja excluir o lançamento selecionado?').then(() => {
      setRemoverLancamento(true);
    });
  };

  useEffect(() => {
    if (removerLancamento && !isEmpty(lancSelecionadoNConciliado)) {
      removerLancamentos(lancSelecionadoNConciliado);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [lancSelecionadoNConciliado, removerLancamento]);

  const removerLancamentoNConciliado = () => {
    promisedAlertWithCatch('Tem certeza que deseja excluir o lançamento selecionado?').then(() => {
      setRemoverLancamento(true);
    });
  };

  const handleChangePlanoConta = () => {
    if (!isEmpty(formProps.getValues().planoConta)) {
      if (!formProps.getValues().planoConta.analitica) {
        alertInformation('A conta selecionada deve ser analítica.');
      }
    }
  };

  return (
    <WindowComponent
      title="Conciliação Contábil - Lançamentos"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMCG(globalReducer.globalParameter),
        () => hasGrupoPlanoContas(globalReducer.globalParameter),
      ]}
      wikiId={1908}
    >
      <Form>
        <Box>
          <Row>
            <Col>
              <PlanoContaIS id="planoConta" formProps={{ ...formProps, validation: { required: true } }} onChange={handleChangePlanoConta} />
            </Col>
          </Row>
          <Row>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true } }}
                id="periodoDe"
                isPrependLabel={true}
                label="Data Inicial"
                placeholder="Informe a Data Inicial"
                type={InputType.DATE}
                onBlur={handleBlurPeriodoDe}
              />
            </Col>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true } }}
                id="periodoAte"
                isPrependLabel={true}
                label="Data Final"
                placeholder="Informe a Data Final"
                type={InputType.DATE}
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <InputGroupInline formProps={formProps} id="historico" isPrependLabel={true} label="Histórico" placeholder="Informe o Histórico" />
            </Col>
          </Row>
          <Row>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true } }}
                id="operacaoConsultaValor"
                inputDropDownProps={{ optionsProvider: OPERACOES_CONSULTA_VALORES }}
                isPrependLabel={true}
                label="Operação"
                placeholder="Informe a Operação"
                type={InputType.DROP_DOWN}
              />
            </Col>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true } }}
                id="valor"
                isPrependLabel={true}
                label="Valor"
                placeholder="Informe o Valor"
              />
            </Col>
          </Row>
          <div className="divider my-3" />
          <Row>
            <Col md="auto">
              <Button className="ml-0" color="primary" onClick={validaBefore}>
                <FontAwesomeIcon icon="sync" /> Consultar
              </Button>
            </Col>
          </Row>
        </Box>
        <TabGroup formProps={formProps}>
          <Tab title="Não Conciliados">
            <Box title="Lançamentos Não Conciliados">
              <div className="table-area">
                <BootstrapTable
                  bordered={true}
                  columns={columnsNConciliados}
                  condensed={true}
                  data={lancamentosNConciliadoList}
                  hover={true}
                  keyField="id"
                  noDataIndication="Nenhum informação a ser exibida."
                  responsive={true}
                  striped={true}
                  tabIndexCell={true}
                  bodyClasses="table-alternate-spaced table"
                  selectRow={selectRowLancamentoNConciliado}
                />
              </div>
              <div key="divFotterKey" className="text-left">
                Registros: {lancamentosNConciliadoList.length}
              </div>
            </Box>
          </Tab>
          <Tab title="Conciliados">
            <Box title="Lançamentos Conciliados">
              <div className="table-area">
                <BootstrapTable
                  bordered={true}
                  columns={columnsConciliados}
                  condensed={true}
                  data={lancamentosConciliadosList}
                  hover={true}
                  keyField="id"
                  noDataIndication="Nenhum informação a ser exibida."
                  responsive={true}
                  striped={true}
                  tabIndexCell={true}
                  bodyClasses="table-alternate-spaced table"
                  selectRow={selectRowLancamentosConciliados}
                />
              </div>
              <div key="divFotterKey" className="text-left">
                Registros: {lancamentosConciliadosList.length}
              </div>
            </Box>
          </Tab>
        </TabGroup>
        <Box>
          <Row className="text-center font-size-md">
            <Col>
              <div className="text-primary">
                <span className="mr-1 badge badge-neutral-primary badge-pill">Saldo Inicial: {formatSaldo(saldoInicial)} </span>
              </div>
            </Col>
            <Col>
              <div className="text-primary">
                <span className="mr-1 badge badge-neutral-primary badge-pill">Saldo a Conciliar: {formatSaldo(saldoAConciliar)} </span>
              </div>
            </Col>
            <Col>
              <div className="text-primary">
                <span className="mr-1 badge badge-neutral-primary badge-pill">Saldo Atual: {formatSaldo(saldoAtual)} </span>
              </div>
            </Col>
            <Col>
              <div className="text-primary">
                <span className="mr-1 badge badge-neutral-primary badge-pill"> Saldo Conciliado: {formatSaldo(saldoConciliado)} </span>
              </div>
            </Col>
          </Row>
        </Box>
      </Form>
      <Modal isOpen={showModalViewConciliar} toggle={handleCloseModal} size="xl">
        <ModalHeader toggle={handleCloseModal} className="font-weight-bold">
          Conciliar Lançamentos
        </ModalHeader>
        <ModalBody>
          <Box>
            <Box>
              <div className="d-flex align-items-center">
                <div className="d-50 rounded-circle bg-neutral-first text-first mr-3 text-center">
                  <FontAwesomeIcon icon={['far', 'bell']} className="font-size-md" />
                </div>
                <div className="flex-grow-1">
                  <span className="mt-4 mb-3 font-weight-bold">Lançamento: </span>
                  {lancSelecionadoNConciliado === null ? null : lancSelecionadoNConciliado.id}
                  <span className="mt-4 mb-3 font-weight-bold"> Data: </span>
                  {lancSelecionadoNConciliado === null ? null : lancSelecionadoNConciliado.dt}
                  <span className="mt-4 mb-3 font-weight-bold"> Valor: </span>
                  {lancSelecionadoNConciliado === null ? null : lancSelecionadoNConciliado.valor}
                  <span className="mt-4 mb-3 font-weight-bold"> Débito: </span>
                  {lancSelecionadoNConciliado === null ? null : lancSelecionadoNConciliado.contaDebito}
                  <span className="mt-4 mb-3 font-weight-bold "> CR Débito: </span>
                  {lancSelecionadoNConciliado === null ? null : lancSelecionadoNConciliado.crDebito}
                  <span className="mt-4 mb-3 font-weight-bold"> Crédito: </span>
                  {lancSelecionadoNConciliado === null ? null : lancSelecionadoNConciliado.contaCredito}
                  <span className="mt-4 mb-3 font-weight-bold"> CR Crédito: </span>
                  {lancSelecionadoNConciliado === null ? null : lancSelecionadoNConciliado.crCredito}
                  <div className="pb-1 mt-2">{informacaoConciliacao}</div>
                </div>
              </div>
            </Box>
            <Box title="Lançamentos para Conciliar">
              <div className="table-area mt-2">
                <BootstrapTable
                  bordered={true}
                  columns={columns}
                  condensed={true}
                  data={lancamentoNConciliadoFilter}
                  hover={true}
                  keyField="id"
                  noDataIndication="Nenhum informação a ser exibida."
                  responsive={true}
                  striped={true}
                  tabIndexCell={true}
                  bodyClasses="table-alternate-spaced table"
                  selectRow={selectRowLancamentoParaConciliar}
                />
              </div>
              <div key="divFotterKey" className="text-left mt-3">
                Registros: {lancamentoNConciliadoFilter.length}
              </div>
            </Box>
            <Box>
              <Row className="font-size-md text-center">
                <Col>
                  <div className="text-danger">
                    <span className="mr-1 badge badge-neutral-primary badge-pill">
                      Soma débitos:{' '}
                      {formatSaldo(
                        lancSelecionadoNConciliado === null ? null : somarDebito === true ? lancSelecionadoNConciliado.valor : valorLancParaConciliar
                      )}
                    </span>
                  </div>
                </Col>
                <Col>
                  <div className="text-primary">
                    <span className="mr-1 badge badge-neutral-primary badge-pill">
                      Soma créditos:{' '}
                      {formatSaldo(
                        lancSelecionadoNConciliado === null ? null : somarCredito === true ? lancSelecionadoNConciliado.valor : valorLancParaConciliar
                      )}
                    </span>
                  </div>
                </Col>
                <Col>
                  <div className="text-warning">
                    <span className="mr-1 badge badge-neutral-primary badge-pill">
                      Saldo Conciliação:
                      {formatSaldo(lancSelecionadoNConciliado === null ? null : lancSelecionadoNConciliado.valor - valorLancParaConciliar)}
                    </span>
                  </div>
                </Col>
              </Row>
            </Box>
          </Box>
        </ModalBody>
        <ModalFooter>
          <div className="float-right">
            <Button type="submit" className="mr-2" color="primary" onClick={conciliar}>
              <FontAwesomeIcon icon="cog" /> Conciliar
            </Button>
            <Button color="danger" onClick={handleCloseModal}>
              <FontAwesomeIcon icon="times" /> Cancelar
            </Button>
          </div>
        </ModalFooter>
      </Modal>
      <Modal isOpen={showModalRemoverConciliacao} toggle={handleCloseModalRemoverConciliados} size="xl">
        <ModalHeader toggle={handleCloseModalRemoverConciliados}>Remover Conciliação</ModalHeader>
        <ModalBody>
          <Box>
            <Box>
              <div className="d-flex align-items-center">
                <div className="d-50 rounded-circle bg-neutral-first text-first mr-3 text-center">
                  <FontAwesomeIcon icon={['far', 'bell']} className="font-size-md" />
                </div>
                <div className="flex-grow-1">
                  <span className="mt-4 mb-3 font-weight-bold"> Lançamento Conciliado </span>
                  <br />
                  <span className="mt-4 mb-3 font-weight-bold"> Nº do Lançamento: </span>
                  {lancSelecionadoConciliado === null ? null : lancSelecionadoConciliado.nro}
                  <span className="mt-4 mb-3 font-weight-bold"> Data: </span>
                  {lancSelecionadoConciliado === null ? null : lancSelecionadoConciliado.dt}
                  <span className="mt-4 mb-3 font-weight-bold"> CR Débito: </span>
                  {lancSelecionadoConciliado === null ? null : lancSelecionadoConciliado.crDebito}
                  <span className="mt-4 mb-3 font-weight-bold"> CR Crédito: </span>
                  {lancSelecionadoConciliado === null ? null : lancSelecionadoConciliado.crCredito}
                  <span className="mt-4 mb-3 font-weight-bold"> Valor: </span>
                  {lancSelecionadoConciliado === null ? null : decimalFormatter(lancSelecionadoConciliado.valor, null)}
                  <br />
                  <span className="mt-4 mb-3 font-weight-bold"> Histórico: </span>
                  {lancSelecionadoConciliado === null ? null : lancSelecionadoConciliado.historico}
                </div>
              </div>
            </Box>
            <Box title="Lançamentos de Créditos">
              <div className="table-area mt-2">
                <BootstrapTable
                  bordered={true}
                  columns={columns}
                  condensed={true}
                  data={lancamentoConciliadoCredito}
                  hover={true}
                  keyField="id"
                  noDataIndication="Nenhum informação a ser exibida."
                  responsive={true}
                  striped={true}
                  tabIndexCell={true}
                  bodyClasses="table-alternate-spaced table"
                />
              </div>
              <div key="divFotterKey" className="text-left mt-3">
                Registros: {lancamentoConciliadoCredito.length}
              </div>
            </Box>
            <Box title="Lançamentos de Débitos">
              <div className="table-area mt-2">
                <BootstrapTable
                  bordered={true}
                  columns={columns}
                  condensed={true}
                  data={lancamentoConciliadoDebito}
                  hover={true}
                  keyField="id"
                  noDataIndication="Nenhum informação a ser exibida."
                  responsive={true}
                  striped={true}
                  tabIndexCell={true}
                  bodyClasses="table-alternate-spaced table"
                />
              </div>
              <div key="divFotterKey" className="text-left mt-3">
                Registros: {lancamentoConciliadoDebito.length}
              </div>
            </Box>
          </Box>
        </ModalBody>
        <ModalFooter>
          <div className="float-right">
            <Button type="submit" className="mr-2" color="primary" onClick={removerConciliacao}>
              <FontAwesomeIcon icon="trash" /> Remover Conciliação
            </Button>

            <Button color="danger" onClick={handleCloseModalRemoverConciliados}>
              <FontAwesomeIcon icon="times" /> Cancelar
            </Button>
          </div>
        </ModalFooter>
      </Modal>
    </WindowComponent>
  );
};

export default ConciliacaoContaView;
